import {waitForRequiredData} from '../utils';
import {attachTaxIdentifierToDropinCheckout} from '../checkout-sessions-api';


export function taxIdentifier(key, configuration, storage, createElement) {

  let nodeToKeep;

  const mountedVersion = {
    instance: undefined,
    data: {}
  };

  const callApiAndMount = async function () {

    // TODO Send to the client when it is required or not, like the shipping choice

    if (storage.checkoutData?.payment?.session.id && storage.checkoutData?.billTo?.address?.country) {

      /*
      taxIdentifierComponent =  digitalRiverPayments3.createElement('taxidentifier', {
      style: styles,
      classes: {
        base: 'cc--base',
        focus: 'cc--focus',
        invalid: 'cc--invalid',
        empty: 'cc--empty',
        complete: 'cc--complete',
        webkitAutofill: 'cc--webkit-autofill'
      },
      taxIdentifier: {
        // "sessionId": "78c37d07-c20c-4be3-aac9-4438aaa40f27", // Has selling entityID : "99b8a047-544e-4ccb-8376-04088b241206",
        type: getURLParamOrDefault('taxIdentifierType', undefined),
        country: getURLParamOrDefault('country', 'IT'),
        sellingEntity: getURLParamOrDefault('sellingEntity', 'DR_IRELAND-ENTITY')
      }
    });
       */

      const customerType = storage.checkoutData.customerType;
      const paymentSessionId = storage.checkoutData?.payment?.session.id;

      // TODO Update if already exists?

      if (!mountedVersion.instance) {
        const instance = createElement('taxidentifier', {
          // TODO Add styles / classes
          taxIdentifier: {
            sessionId: paymentSessionId,
            type: customerType,
            //country: storage.checkoutData?.billTo?.address?.country,
            //sellingEntity: storage?.checkoutData?.sellingEntity?.id
          }
        });
        instance.on('change', function (event) {
          console.log('CHANGE', event);
          if (event.complete) {
            mountedVersion.data[event.identifier.type] = {
              value: event.identifier.value,
              type: event.identifier.type,
            };
            console.log('Tax identifier data', mountedVersion.data);

            // TODO Save here or on a next function like address?
          } else {
            delete mountedVersion.data[event.identifier.type];
            console.log('Tax identifier data', mountedVersion.data);
          }
        });

        instance.mount(nodeToKeep.id);

        mountedVersion.instance = instance;

      }  else {
        /* mountedVersion.instance.update({
          taxIdentifier: {
            sessionId: paymentSessionId,
            type: customerType,
          }
        });*/
      }

    }

  };

  return {
    mount: (node) => {

      if (typeof node === 'string') {
        node = document.getElementById(node);
      }

      if (!(node instanceof HTMLElement)) {
        throw new Error('mount() requires a valid HTMLElement or id attribute.');
      }

      nodeToKeep = node;

      waitForRequiredData(storage).then( () => {
        callApiAndMount(storage.checkoutData);
      });

      storage.subscribers.push(callApiAndMount);


    },
    done : async function () {

      console.log('mountedVersion', mountedVersion);

      // TODO Get the required data from onReady event and return false if required data is not filled out OR rely on API to check that?

      const taxIdentifier = {
        taxIdentifiers: Object.entries(mountedVersion.data).map(([key, value]) => {
          console.log('entry', key, value);
          return {
            type: value.type,
            value: value.value
          };
        })
      };

      // TODO Attach to API
      try {
        const response = await attachTaxIdentifierToDropinCheckout(taxIdentifier, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);
        // TODO Check response code
        return true;
      } catch (e) {
        return false;
      }
    }

  };

}