import {validateClientDomElement, waitForRequiredData} from '../utils';
import {handleRedirectComplete, handleRedirectResponse} from './payment-api-call';
import {createOnCancel, createOnError, createOnReady, createOnSuccess, createOnUpdateRequired} from './payment-events';
import {getStoredPaymentMethods} from './checkout-session-payment-adapter';

export function payment(key, configuration, storage, createDropin, handleNextAction, createElement) {

  return {
    mount: (node) => {

      validateClientDomElement(node);

      const dropinOnCancel = createOnCancel(storage);

      handleRedirectComplete(storage.controller, storage, dropinOnCancel);

      handleRedirectResponse(storage.controller, storage);

      waitForRequiredData(storage).then(() => {

        const sessionId = storage.checkoutData.payment.session.id;

        const storedPaymentMethods = getStoredPaymentMethods(storage);

        storage.addedSubscriber = false;

        const dropinInstance = createDropin({
          sessionId: sessionId,
          onError: createOnError(storage),
          onCancel: dropinOnCancel,
          onReady: createOnReady(storage),
          onSuccess: createOnSuccess(storage, configuration, handleNextAction, createElement),
          options: {
            showSavePaymentAgreement: storage.checkoutData?.customerId?.length > 0,
            redirect: {
              disableAutomaticRedirects: true
            },
            storedPaymentMethods: storedPaymentMethods,
          },
          onUpdateRequired: createOnUpdateRequired(storedPaymentMethods, configuration, storage)
        });

        dropinInstance.mount(node);

        storage.dropinInstance = dropinInstance;

      });


    },
  };
}


