import {appendAddress} from './Address';
import {attachAddressToDropinCheckout} from '../checkout-sessions-api';
import {validateClientDomElement, waitForRequiredData} from '../utils';
import {billingAddressOnlyMode} from './address-utils';

export function defaultError() {
  return {
    shippingAddress: {},
    billingAddress: {},
    main: ''
  };
}

// Comes from: https://github.digitalriverws.net/EnterpriseAdmin/hydra/blob/8da7d8341599d0831bf7b1eadd6de78c99bd4007/common-client/src/utils/email.ts
// eslint-disable-next-line no-useless-escape
const EMAIL_REGEX = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

function getBillingAddressSchemaProperties(billingAddressSchemaRaw) {
  const properties = {
    email: {type: 'string', description: 'persons email', minLength: 0, maxLength: 128, pattern: EMAIL_REGEX}, // TODO EMAIL PATTERN // TODO REMOVE WHEN COMING FROM API
    phoneNumber: {type: 'string', description: 'persons email', minLength: 0, maxLength: 128},
    firstName: {type: 'string', description: 'persons first name', minLength: 0, maxLength: 128},
    lastName: {type: 'string', description: 'persons last name', minLength: 0, maxLength: 128},
    ...JSON.parse(billingAddressSchemaRaw).properties
  };

  delete properties.name;

  return properties;
}

function getShippingAddressSchemaProperties(shippingAddressSchemaRaw) {
  const properties = { // EMAIL PATTERN: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    email: {type: 'string', description: 'persons email', minLength: 0, maxLength: 128, pattern: EMAIL_REGEX}, // TODO EMAIL PATTERN // TODO REMOVE WHEN COMING FROM API
    phoneNumber: {type: 'string', description: 'persons email', minLength: 0, maxLength: 128},
    firstName: {type: 'string', description: 'persons first name', minLength: 0, maxLength: 128},
    lastName: {type: 'string', description: 'persons last name', minLength: 0, maxLength: 128},
    ...JSON.parse(shippingAddressSchemaRaw).properties
  };

  delete properties.name;

  return properties;
}

function validateInputAgainstSchema(type, properties, requiredFields, storage, errors) {
  Object.keys(properties).forEach(key => {

    const isRequired = requiredFields.includes(key);
    const value = storage?.[type]?.[key];
    const pattern = properties[key].pattern;

    if (isRequired && (typeof value === 'undefined' || value.length === 0)) {
      errors[type][key] = {
        code: 'LENGTH',
        message: ''
      };
    } else if (typeof pattern !== 'undefined' && !new RegExp(pattern).test(value)) {
      //errors[type][key] = storage.translations.error.validationError; // TODO Different error?
      errors[type][key] = {
        code: 'PATTERN',
        message: storage.translations.error.validationError
      };
    }

  });
}

function getBillingRequiredFields(billingAddressSchemaRaw) {
  const billingRequiredFields = JSON.parse(billingAddressSchemaRaw).required;

  billingRequiredFields.push( // TODO REMOVE WHEN COMING FROM API
    'email',
    'phoneNumber',
    'firstName',
    'lastName'
  );
  return billingRequiredFields;
}

function getShippingRequiredFields(shippingAddressSchemaRaw) {
  const shippingRequiredFields = JSON.parse(shippingAddressSchemaRaw).required;

  shippingRequiredFields.push( // TODO REMOVE WHEN COMING FROM API
    'email',
    'phoneNumber',
    'firstName',
    'lastName'
  );
  return shippingRequiredFields;
}

function distinctPredicate() {
  return (x, i, a) => a.indexOf(x) === i;
}

function mergeShippingRequiredFieldsWhenSameAddress(mergeRequiredFields, billingRequiredFields, shippingRequiredFields) {
  if (mergeRequiredFields) {
    return shippingRequiredFields.concat(billingRequiredFields).filter(distinctPredicate());
  } else {
    return shippingRequiredFields;
  }
}

function addressWithValidProperties(address) {
  const notAValidAddressAPIProperty = ['email', 'phoneNumber', 'firstName', 'lastName', 'phone', 'name', 'address', 'organization'];

  const newAddress = Object.assign({}, address);
  notAValidAddressAPIProperty.forEach( i => delete newAddress[i] );

  return newAddress;
}

function setValueWhenOnlySingleEnumValue(schema, storage, addressType) {
  Object.keys(schema).forEach(key => {
    const property = schema[key];
    if (typeof property.enum !== 'undefined' && property.enum.length === 1) {
      storage[addressType][key] = property.enum[0];
    }
  });
}

const BILLING_ADDRESS_TYPE = 'billingAddress';
const SHIPPING_ADDRESS_TYPE = 'shippingAddress';

function shouldDisplaySchema(checkoutData) {
  return typeof checkoutData !== 'undefined' && checkoutData.shippingAddressSchema && checkoutData.billingAddressSchema;
}

function setErrorMessageAndRemount(storage, callApiAndMount, errorMessage) {
  callApiAndMount(storage.checkoutData, {
    ...defaultError(),
    main: errorMessage
  });
}

function getBillingAddressToUse(checkoutData, storage) {
  if (billingAddressOnlyMode(checkoutData)) {
    return storage.billingAddress;
  }

  return storage.shippingAddressTypeSameAsBilling ? storage.shippingAddress : storage.billingAddress;
}

export function address(key, configuration, storage) {

  let nodeToKeep;

  storage.shippingAddress = {};
  storage.billingAddress = {};
  storage.shippingAddressTypeSameAsBilling = true;

  const callApiAndMount = async function (checkoutData, errors) {
    if (shouldDisplaySchema(checkoutData)) {
      const billingAddressSchemaRaw = checkoutData.billingAddressSchema;
      const shippingAddressSchemaRaw = checkoutData.shippingAddressSchema;

      const billingAddressSchemaProperties = getBillingAddressSchemaProperties(billingAddressSchemaRaw);
      const shippingAddressSchemaProperties = getShippingAddressSchemaProperties(shippingAddressSchemaRaw);

      const shippingRequiredFields = mergeShippingRequiredFieldsWhenSameAddress(
        storage.shippingAddressTypeSameAsBilling || billingAddressOnlyMode(checkoutData),
        getBillingRequiredFields(billingAddressSchemaRaw),
        getShippingRequiredFields(shippingAddressSchemaRaw)
      );

      const billingRequiredFields = getBillingRequiredFields(billingAddressSchemaRaw);

      appendAddress(nodeToKeep, {
        errors: typeof errors === 'undefined' ? defaultError() : errors,
        billingAddressOnlyMode: billingAddressOnlyMode(checkoutData),
        savedAddresses: checkoutData?.options?.addresses,
        translations: storage.translations,
        shippingAddressSchema: shippingAddressSchemaProperties,
        billingAddressSchema: billingAddressSchemaProperties,
        shippingRequiredFields: shippingRequiredFields,
        billingRequiredFields: billingRequiredFields,
        isShippingAddressSameAsBilling: storage.shippingAddressTypeSameAsBilling,
        updateAddress: function (addressType, address) {
          storage[addressType] = address;
        },
        getAddress: function (addressType) {
          return storage[addressType];
        },
        handleShippingAddressTypeChange: function (selected) {
          storage.shippingAddressTypeSameAsBilling = selected;
          callApiAndMount(checkoutData);
        },
        updateErrors: function (errors) {
          callApiAndMount(checkoutData, errors);
        },
        updateAddressAndErrors: function (addressType, address, errors) {
          storage[addressType] = address;
          callApiAndMount(checkoutData, errors);
        }
      });

    }

  };

  return {
    mount: (node) => {

      nodeToKeep = validateClientDomElement(node);

      storage.subscribers.push(callApiAndMount);

      waitForRequiredData(storage).then( () => {
        callApiAndMount(storage.checkoutData);
      });

    },
    done : async function () {

      const errors = defaultError();

      const checkoutData = storage.checkoutData;
      if (!shouldDisplaySchema(checkoutData)) {
        return true;
      }

      setErrorMessageAndRemount(storage, callApiAndMount, '');

      const billingAddressSchemaRaw = checkoutData.billingAddressSchema;
      const shippingAddressSchemaRaw = checkoutData.shippingAddressSchema;

      const billingRequiredFields = getBillingRequiredFields(billingAddressSchemaRaw);
      const shippingRequiredFields = mergeShippingRequiredFieldsWhenSameAddress(
        storage.shippingAddressTypeSameAsBilling,
        getBillingRequiredFields(billingAddressSchemaRaw),
        getShippingRequiredFields(shippingAddressSchemaRaw)
      );

      const billingAddressProperties = getBillingAddressSchemaProperties(billingAddressSchemaRaw);
      const shippingAddressProperties = getShippingAddressSchemaProperties(shippingAddressSchemaRaw);

      if (checkoutData.requiresShipping === true) {
        setValueWhenOnlySingleEnumValue(shippingAddressProperties, storage, SHIPPING_ADDRESS_TYPE);
        validateInputAgainstSchema(SHIPPING_ADDRESS_TYPE, shippingAddressProperties, shippingRequiredFields, storage, errors);
      }

      if (checkoutData.requiresShipping === false || storage.shippingAddressTypeSameAsBilling === false) {
        setValueWhenOnlySingleEnumValue(billingAddressProperties, storage, BILLING_ADDRESS_TYPE);
        validateInputAgainstSchema(BILLING_ADDRESS_TYPE, billingAddressProperties, billingRequiredFields, storage, errors);
      }

      // If not valid, then notify UI
      if (Object.keys(errors.shippingAddress).length > 0 || Object.keys(errors.billingAddress).length > 0) {
        callApiAndMount(storage.checkoutData, errors);
        return false;
      }

      const billingAddressToUse = getBillingAddressToUse(checkoutData, storage);

      const billingAddressFromState = addressWithValidProperties(billingAddressToUse);
      const shippingAddressFromState = addressWithValidProperties(storage.shippingAddress);

      const address = {
        'email': billingAddressToUse.email,
        'billTo': {
          'address': billingAddressFromState,
          'organization' : shippingAddressFromState.organization,
          'email':billingAddressToUse.email,
          'name': billingAddressToUse.firstName + ' ' + billingAddressToUse.lastName,
          'phone': billingAddressToUse.phoneNumber
        },
        'shipTo': {
          'address': shippingAddressFromState,
          'organization' : shippingAddressFromState.organization,
          'email': storage.shippingAddress.email,
          'name': storage.shippingAddress.firstName + ' ' + storage.shippingAddress.lastName,
          'phone': storage.shippingAddress.phoneNumber
        }
      };

      if (checkoutData.requiresShipping === false) {
        delete address.shipTo;
      }

      try {

        const response = await attachAddressToDropinCheckout(address, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);
        if (response.status !== 200) {
          setErrorMessageAndRemount(storage, callApiAndMount, storage.translations.error.notSaved);
          return false;
        }
        storage.notify();
        return true;

      } catch (e) {
        setErrorMessageAndRemount(storage, callApiAndMount, storage.translations.error.notSaved);
        return false;
      }

    }

  };

}